const axios = require('axios')

export default {
    install(Vue, options) {
        Vue.prototype.$common = {
            //---------------------------------------------------------------------------------------------------


            //
            host: "http://127.0.0.1:7001",

            //url
            bookDetailUrl: "/api/api/search/getBookDetail",

            //url
            classUrl: "/api/api/search/getTopLendClass",

            //url
            searchBookUrl: "/api/api/search/getSearchRes",

            //url
            classDetailUrl: "/api/api/search/getTopLendDetail",

            //url
            nowBorrow: "/api/api/fetch/nowBorrow",

            //url
            hisBorrow: "/api/api/fetch/hisBooks",

            //url
            updateAll: "/api/api/update/all",

            //url
            renewUrl: "/api/api/update/tryRenew",

            //imgurl
            libHost: "http://opac.lib.neau.edu.cn",

            //url
            bookNoCoverUrl: "http://opac.lib.neau.edu.cn/m/mopac/inner/images/no-book.jpg",

            //path
            bookNoCoverPath: "/m/mopac/inner/images/no-book.jpg",

            //url
            rebindUrl: "/api/api/rebind",

            //url
            checkUserUrl: "/api/api/checkUser",

            //
            person: {
                stuId: '',
                pswd: '',
                name: '',
            },

            // 7
            warn_days: 7,

            //bookMorePageInfo
            bookMorePageInfo: {
                books: [],
                number: 0,
                title: '',
            },



            //---------------------------------------------------------------------------------------------------


            //
            checkCover: async function (imgUrl) {
                if (imgUrl === undefined) {
                    return this.bookNoCoverUrl;
                }
                if (imgUrl === this.bookNoCoverUrl) {
                    return this.bookNoCoverUrl;
                }

                //imgUrl
                try {
                    let res = await axios({
                        method: "get",
                        url: imgUrl,
                    })
                    //
                    if (res.status === 200) {
                        return imgUrl;
                    }
                }
                catch (err) {
                    //
                    //  console.log('url:', imgUrl)
                    return this.bookNoCoverUrl
                }

            },

            //bookId bookImgUrl   :'id=xxxxxxxxxx'
            getbookImgUrl: async function (bookId) {
                let self = this;

                //bookidurl
                let getImgUrl = async function (bookId) {
                    let res = await axios({
                        methods: "get",
                        url: self.bookDetailUrl,
                        params: {
                            bookId: bookId
                        }
                    });
                    return res.data[0].imgurl;
                }

                //bookid url
                let patt = /\d+/g;
                bookId = bookId.match(patt)[0];
                let imgUrl = await getImgUrl(bookId);
                if (this.bookNoCoverPath === imgUrl) {
                    imgUrl = self.libHost + imgUrl
                }

                //url
                imgUrl = await this.checkCover(imgUrl);

                return imgUrl
            },



            //
            urlName: {
                loginPage: 0,
                searchPage: 1,
                searchResult: 2,
                bookDetail: 4,
                personalPage: 2,
                bookMorePage: 3,
                beforeUrl: '',
            },
            /**
             * true
             * false
             */
            isBackUrl: function (myUrlName) {
                if (this.urlName.beforeUrl === '') {
                    this.urlName.beforeUrl = myUrlName;
                    return false;
                }
                let beforeUrlName = this.urlName.beforeUrl;
                if (this.urlName[myUrlName] <= this.urlName[beforeUrlName]) {
                    this.urlName.beforeUrl = myUrlName;
                    return true;
                }

                this.urlName.beforeUrl = myUrlName;
                return false;
            },


            //---------------------------------------------------------------------------------------------------

            //searchResult
            searchResult: {
                //
                myScrollTop: 0,
                //
                classes: [],
                //
                books: [],
                //
                booksNumber: 0,
                //
                isEmpty: true,
                //
                changeTimes: 0,
                //
                loading: false,
                //busy
                busy: false,
                //url
                nowUrl: "",
                //
                nowPage: 1,
                //,
                searchStr: "",
                //
                totalPages: 0,
                //
                claNo: "",
                //
                beDestroyed: false,
            },

            //personalPage
            personalPage: {
            }
        };
    }
}


